@import "../../../Style/Colors.scss";
@import "../../../Style/Variables.scss";

.current_news {
  padding-top: 196px;
  background-color: $background;
}

.current_news_carusel {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.current_news_content {
  width: 100%;
}

.current_news_carusel_title {
  @extend %text-style-36px;
  color: $color-content;
  width: 780px;
  margin-bottom: 48px;
}

.current_news_carusel_text {
  @extend %text-style-24px;
  color: $color-text;
  width: 780px;
  margin-bottom: 32px;
}

.current_news_img {
  margin-bottom: 32px;
}

.current_news_img_text {
  @extend %text-style-24px;
  margin-top: 48px;
}

.current_news_left {
  width: 64px;
  height: 64px;
  padding-right: 60px;
}

.current_news_rigth {
  width: 64px;
  height: 64px;
  padding-left: 60px;
}

.current_news_ {
  display: flex;
}

.slick-next_news {
  position: fixed;
  width: 64px;
  height: 64px;
  z-index: 11;
  opacity: 0.3;
}

.slick-prev_news {
  position: fixed;
  width: 64px;
  height: 64px;
  z-index: 11;
  opacity: 0.3;
}

.slick-next_news:hover {
  opacity: 1;
}

.slick-prev_news:hover {
  opacity: 1;
}
